import Vue from 'vue'
import App from './App.vue'
import store from './store'

Vue.config.productionTip = false

const BellatrixInstance = new Vue({
    store,
    components: { App },
    data: () => ({ config: {} }),
    render: function(createElement) {
        return createElement(App, {
            props: {
                config: this.config
            }
        })    
    },
})

export default class BTXGenerator {
    constructor(selector: string, config: any) {
        Vue.set(BellatrixInstance, 'config', {
            ...config,
            // id is optional. but allows setting the app's `id` dynamically to same `id`
            // as the context page placeholder (which app replaces), for consistency
            // for this, you have to assign `config.id` to App's `$el` in its `mounted()`
            id: selector.replace('#', '')
            // or document.querySelector(selector).id || 'someClassApp'
        });
        BellatrixInstance.$mount(selector);
        return BellatrixInstance;
    }
}
